import React from "react";
import { 
    Form, FormGroup, FormText, FormFeedback,
    Container, Col, Label, Input, Button,
} from "reactstrap";

export class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            validate: {
                emailState: '',
                passwordState: '',
            },
        }
        this.handleChange = this.handleChange.bind(this);
    }


    validateEmail(e) {
        const emailRex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        const { validate } = this.state
        if (e.target.value.length < 1) {
            validate.emailState = 'An email is required'
        } else if (emailRex.test(e.target.value)) {
            validate.emailState = ''
        } else {
            validate.emailState = 'Email is not formatted correctly'
        }
        this.setState({ validate })
    }

    validatePassword(e) {
        const passwordRex = /^([a-zA-Z]{5,8})$/;
        const { validate } = this.state
        if (e.target.value.length < 1) {
            validate.passwordState = 'A password is required'
        } else if (e.target.value.length < 5) {
            validate.passwordState = 'Password is too short'
        } else if (e.target.value.length > 8) {
            validate.passwordState = 'Password is too long'
        } else if (passwordRex.test(e.target.value)) {
            validate.passwordState = ''
        } else {
            validate.passwordState = 'Password is not all letters'
        }
        this.setState({ validate })
    }

    disableSubmit = () => {
      return (
        this.state.email.length === 0 || 
        this.state.password.length === 0 ||
        this.state.validate.emailState.length !== 0 || 
        this.state.validate.passwordState.length !== 0
      );
    }

    handleChange = async (event) => {
        const { target } = event;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const { name } = target;
        await this.setState({
          [ name ]: value,
        });
    }
    
    submitForm(e) {
        e.preventDefault();
        this.props.userHasAuthenticated(true);
    }
    
    render() {
        const { email, password } = this.state;
        return (
          <Container className="App">
            <h2>Sign In</h2>
            <Form className="form" onSubmit={ (e) => this.submitForm(e) }>
              <Col>
                <FormGroup>
                  <Label>Username</Label>
                  <Input
                    type="email"
                    name="email"
                    id="exampleEmail"
                    placeholder="myemail@email.com"
                    value={ email }
                    valid={ this.state.email !== '' && this.state.validate.emailState === '' }
                    invalid={ this.state.validate.emailState !== '' }
                    onChange={ (e) => {
                                this.validateEmail(e)
                                this.handleChange(e)
                              } }
                  />
                  <FormFeedback>{ this.state.validate.emailState }</FormFeedback>
                  <FormFeedback valid>
                    Your email is formatted correctly.
                  </FormFeedback>
                  <FormText>Your username is your registered email.</FormText>
                </FormGroup>
              </Col>
              <Col>
                <FormGroup>
                  <Label for="examplePassword">Password</Label>
                  <Input
                    type="password"
                    name="password"
                    id="examplePassword"
                    placeholder="********"
                    value={ password }
                    invalid={ this.state.validate.passwordState !== '' }
                    onChange={ (e) => {
                                this.validatePassword(e)
                                this.handleChange(e)
                              } }
                    />
                    <FormFeedback>{ this.state.validate.passwordState }</FormFeedback>
                    <FormText>Your password is case insensitive and 5 to 8 letters.</FormText>
                </FormGroup>
              </Col>
              <Button disabled={this.disableSubmit()}>Login</Button>
          </Form>
          </Container>
        );
    }
}
