import React, { Component } from 'react';
import { 
  Button, Collapse, UncontrolledDropdown, DropdownMenu, DropdownItem, DropdownToggle,
  Navbar, NavbarBrand, NavbarToggler, NavItem, NavLink 
} from 'reactstrap';
import { Link } from 'react-router-dom';

export class NavMenu extends Component {

  constructor (props) {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      collapsed: true,
    };
  }

  toggleNavbar () {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }

  render () {
    const here = this.props.location.pathname;

    return (
      <header>
        <Navbar className="navbar-expand-sm navbar-toggleable-sm ng-white border-bottom box-shadow mb-3" light>
            <NavbarBrand tag={Link} to="/"><img src="air-fund-logo-light.png" alt="Air Fund"></img></NavbarBrand>
            <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
            <Collapse className="d-sm-inline-flex flex-sm-row justify-content-between" isOpen={!this.state.collapsed} navbar>
              <ul className="navbar-nav flex-grow">
                <NavItem>
                  <NavLink disabled={here === "/"} tag={Link} className="text-dark" to="/">My Fund</NavLink>
                </NavItem>
                <NavItem>
                  <UncontrolledDropdown setActiveFromChild nav inNavbar>
                    <DropdownToggle tag="a" className="nav-link text-dark" caret>Activity</DropdownToggle>
                    <DropdownMenu>
                      <DropdownItem disabled={here === "/transactions"} tag={Link} to="/transactions">My Transactions</DropdownItem>
                      <DropdownItem disabled={here === "/summary"} tag={Link} to="/summary">My Summary</DropdownItem>
                    </DropdownMenu>
                  </UncontrolledDropdown>
                </NavItem>
                <NavItem>
                  <NavLink disabled={here === "/accounts"} tag={Link} className="text-dark" to="/accounts">My Accounts</NavLink>
                </NavItem>
              </ul>
              <ul className="navbar-nav">
              <li><Button onClick={() => this.props.userHasAuthenticated(false)}>Logout</Button></li>
             </ul>
          </Collapse>
        </Navbar>
      </header>
    );
  }
}
