import React from 'react';
import { Line } from 'react-chartjs-2';

const lineoptions = { 
  scales: {
      yAxes: [{
          ticks: {
              beginAtZero: true
          }
      }]
  }
};

const linedata = {
labels: ['2020', '2024', '2028', '2032', '2036', '2040'],
datasets: [
  {
    label: '4%',
    fill: false,
    borderColor: 'red',
    data: [1000, 1160, 1800, 2100, 2500, 4000]
  },{
    label: '8%',
    fill: false,
    borderColor: 'blue',
    data: [1020, 1260, 2100, 2700, 3100, 4600]
  },{
    label: '12%',
    fill: false,
    borderColor: 'green',
    data: [1090, 1480, 2900, 3200, 4200, 5400]
  }
]
};

export class Home extends React.Component {
  render() {
    return (
<Line data={linedata} options={lineoptions} />
    );
  }
}
