import React from 'react';
import { Route, BrowserRouter, withRouter } from 'react-router-dom';
import { Col, Row, Container } from 'reactstrap';
import { NavMenu } from './components/NavMenu';
import { Home } from './components/Home';
import { Summary } from './components/Summary';
import { Transactions } from './components/Transactions';
import { Accounts } from './components/Accounts';
import { Login } from './components/Login';

const NavMenuWithRouter = withRouter(NavMenu);

function Footer (props) {
  return (
<footer>
<div className="row">
  <div className="col-sm-12">
    <hr/>
    <p className="text-muted">Investment for your future from what we buy today.</p>
    <p className="text-center text-info">Â© AirFund 2019</p>
  </div>
</div>
</footer>
  );
}

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      route: '',
      isAuthenticated: false
    };
  }
  
  setRoute = pathname => {
    this.setState({ route: pathname });
  }

  userHasAuthenticated = authenticated => {
    this.setState({ isAuthenticated: authenticated });
  }
  
  render () {
    if (this.state.isAuthenticated) {
      return (
<BrowserRouter basename={this.props.baseUrl}>
  <Container>
    <NavMenuWithRouter userHasAuthenticated={ this.userHasAuthenticated } />
    <main>
      <Route exact path='/' component={Home} />
      <Route path='/summary' component={Summary} />
      <Route path='/transactions' component={Transactions} />
      <Route path='/accounts' component={Accounts} />
    </main>
    <Footer/>
  </Container>
</BrowserRouter>
);
    } else {
      return (
<Container>
  <Row className="align-items-center">
  <Col sm="4">
    <p className="text-center"><img src="air-fund-logo-light.png" alt="Air Fund" /></p>
  </Col>
  <Col>
      <Login userHasAuthenticated={ this.userHasAuthenticated }/>
    </Col>
  </Row>
  <Footer/> 
</Container>
      );
    }
  }
}
